<script>
import filtersMixin from '@/mixins/filtersMixin'
import {
    mapClientFields
} from '@/store/modules/client'

// Icon
import InformationOutline from 'mdi-vue/InformationOutline';

export default {
    components: {
        InformationOutline
    },
    mixins: [
        filtersMixin
    ],
    data: () => ({
        isDragging: false,
        dragCount: 0,
        files: [],
        file: [],
        uploadedFiles: [],
        inProgress: false,
        uploadProgressValue: 0,
        dialog: false,
        fullSizeImg: null,
        delFile: null,
        dialogConfirmDel: false,

        sorting: {
            sort_by: 'created_at',
            sort_order: 'desc'
        }

    }),
    computed: {
        ...mapClientFields([
            'client_data.firstname',
            'client_data.lastname',
            'client_data.email',
            'clientid'
        ]),
        clientInitials() {
            return this.firstname.charAt(0) + this.lastname.charAt(0)
        },
        id() {
            return this.$route.params.id;
        },
        windowWidth() {
            return window.innerWidth;
        }
    },
    beforeDestroy() {
        // clearInterval(this.interval);
    },
    async mounted() {
        // await this.getFiles();
    },
    methods: {
        sortList(sort_by) {
            function compareValues(key, order = 'asc') {
                return function innerSort(a, b) {
                    // if (!a.hasOwnProperty(key) || !b.hasOwnProperty(key)) {
                    //     // property doesn't exist on either object
                    //     return 0;
                    // }

                    const varA = (typeof a[key] === 'string')
                        ? a[key].toUpperCase() : a[key];
                    const varB = (typeof b[key] === 'string')
                        ? b[key].toUpperCase() : b[key];

                    let comparison = 0;
                    if (varA > varB) {
                        comparison = 1;
                    } else if (varA < varB) {
                        comparison = -1;
                    }
                    return (
                        (order === 'desc') ? (comparison * -1) : comparison
                    );
                };
            }

            const sort_order = this.sorting.sort_order === 'asc' ? 'desc' : 'asc'
            this.sorting.sort_by = sort_by
            this.sorting.sort_order = sort_order
            this.uploadedFiles = this.uploadedFiles.sort(compareValues(sort_by, sort_order))
        },
        getFileExtension(filename) {
            const data = filename.split('.')
            if (data[1]) return data[1]
            return false
        },
        isOwnedByWriterOrAdmin(owner) {
            return /^\d+$/.test(owner)
        },
        // getFiles() {
        //     new Promise(() => {
        //         const vm = this;
        //         Api.post('/api/cp/file/list-files', {
        //             id: this.$route.params.id
        //         }).then((response) => {
        //             vm.uploadedFiles = response.data;
        //         }).catch((error) => {
        //             eventBus.$emit('showSnackBar', error, 'error');
        //         })
        //     })
        // },
        onDragEnter(e) {
            e.preventDefault();

            this.dragCount += 1;
            this.isDragging = true;
        },
        onDragLeave(e) {
            e.preventDefault();

            this.dragCount -= 1;
            if (this.dragCount <= 0) {
                this.isDragging = false;
            }
        },
        onDrop(e) {
            e.preventDefault();
            e.stopPropagation();

            this.isDragging = false;

            const { files } = e.dataTransfer;

            Array.from(files).forEach((file) => this.addUploadFiles(file));
            this.upload();
        },
        addUploadFiles(file) {
            this.files.push(file);

            // const img = new File(this.files, '');
            const reader = new FileReader();
            reader.onload = (e) => this.file.push(e.target.result);
            reader.readAsDataURL(file);
        },
        onInputChange(e) {
            const { files } = e.target;
            Array.from(files).forEach((file) => this.addUploadFiles(file));

            this.upload();
        },
        getFileOwnerClass(table, owner) {
            if (table === 'file_add' && owner === 'client') {
                return 'loading-file__client'
            }
            if (table === 'file_main') {
                return 'loading-file__support'
            }
            if (table === 'file_fax') {
                return 'loading-file__client'
            }

            return 'loading-file__support'
        },
        upload() {
            const vm = this;
            vm.inProgress = true;
            const formData = new FormData();
            vm.files.forEach((file) => {
                formData.append(`uploaded[${file.name}]`, file)
            });
            formData.append('orderid', this.id);
            vm.$http.post('/api/cp/file/upload', formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                },
                onUploadProgress: (progressEvent) => {
                    vm.uploadProgressValue = Math.floor((progressEvent.loaded * 100) / progressEvent.total);
                }
            })
                .then(() => {
                    vm.success();
                    vm.file = [];
                    vm.files = [];
                    // eventBus.$emit('showSnackBar', 'Files were successfully uploaded', 'success');
                })
                .catch(() => {
                    vm.file = [];
                    // vm.files = [];
                    // eventBus.$emit('showSnackBar', error, 'error');
                })
                .then(() => {
                    vm.inProgress = false;
                })
        },
        success() {
            this.getFiles();
        },
        confirmDelFile(file) {
            this.delFile = file;
            this.dialogConfirmDel = true;
        },
        deleteFile() {
            const vm = this
            this.$http.delete(`/api/cp/file/delete/${vm.delFile.id}`)
                .then(() => {
                    for (let i = 0; i < this.uploadedFiles.length; i += 1) {
                        if (this.uploadedFiles[i].id === vm.delFile.id) {
                            // eventBus.$emit('showSnackBar', `File "${this.uploadedFiles[i].filename}" has been deleted`, 'success');
                            this.delFile = null;
                            this.dialogConfirmDel = false;
                            this.uploadedFiles.splice(i, 1);
                            return;
                        }
                    }
                })
                .catch(() => {
                    // eventBus.$emit('showSnackBar', error.message, 'error');
                })
        },
        showFullImg(file) {
            this.fullSizeImg = file;
            console.log(file)
            this.dialog = true;
        }
    }
}
</script>
