<template lang="html">
    <div class="message-details">
        <header class="orders-details-tabs__tab-header">
            <p class="orders-details-tabs__tab-description">
                <InformationOutline />
                This is the communication log of the project. All your as well as the writers' messages are shown here.
            </p>
        </header>

        <section class="tickets page-content wrapper">
            <div class="page-content__col12">
                <!-- <Noresults v-if="messages.length === 0" type="writers-messages"/> -->
                <div>
                    <div class="tickets">
                        <div class="tickets_card">
                            <div class="tickets_card__info">
                                <transition name="slide-y-transition">
                                    <div class="tickets_card__messages">
                                        <div>
                                            <div
                                                v-for="(message, message_index) in messages"
                                                :key="message_index"
                                                class="tickets_card__messages-item"
                                                :class="{ 'tickets_card__messages-item__support': message.owner !== 'client' }"
                                            >
                                                <div class="tickets_card__messages-item__inside">
                                                    <div class="tickets_card__messages-item__content">
                                                        <div class="tickets_card__messages-item__content-header">
                                                            <span
                                                                v-if="message.created_at_local"
                                                                class="tickets_card__messages-item__content-header__time"
                                                            >
                                                                <span>{{ message.created_at_local.date | moment_from }} ago</span>
                                                                <small>
                                                                    {{ message.created_at_local.date | moment_short_text }}
                                                                </small>
                                                            </span>
                                                            <span
                                                                v-if="message.owner === `client`"
                                                                class="tickets_card__messages-item__content-header__name"
                                                            >
                                                                {{ `${firstname}` }}
                                                            </span>
                                                            <span
                                                                v-else
                                                                class="tickets_card__messages-item__content-header__name"
                                                            >
                                                                Support
                                                            </span>
                                                        </div>
                                                        <div
                                                            class="tickets_card__messages-item__content-text"
                                                            v-html="message.details"
                                                        />
                                                    </div>

                                                    <div class="tickets_card__messages-item__avatar">
                                                        <div
                                                            v-if="message.owner === 'client'"
                                                            class="tickets_card__messages-item__avatar-user"
                                                        >
                                                            <span>{{ `${firstname.substr(0, 1) + lastname.substr(0, 1)}` }}</span>
                                                        </div>
                                                        <div
                                                            v-else
                                                            class="tickets_card__messages-item__avatar-service"
                                                        />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <NoResult
                                            v-if="!messages.length"
                                            type="writers-messages"
                                        />
                                        <div class="tickets_card__messages-actions">
                                            <form-input
                                                v-model="message"
                                                legend="Message"
                                                type="text"
                                                name="message"
                                                :field.sync="message"
                                                :errors="validationMessage($v.message)"
                                                @input.native="$v.message.$touch()"
                                                @keyup.native="keyListner($event)"
                                                @blur.native="$v.message.$touch()"
                                            />
                                            <button
                                                class="btn-base btn-base_normal btn-base_colored tickets_card__messages-actions-btn"
                                                @click.prevent="sendMessage(messages.ticketid)"
                                            >
                                                <button-loader v-if="loadingBtn" />
                                                send
                                            </button>
                                        </div>
                                    </div>
                                </transition>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import { eventBus } from '@/helpers/event-bus'
import Api from '@/helpers/api/index.js';

// Mixins
import filtersMixin from '@/mixins/filtersMixin.js'

// Validation
import { validationMixin } from 'vuelidate'
import { validationMessage } from 'vuelidate-messages'
import { required } from 'vuelidate/lib/validators'
import { formMessages } from '@/validation/support/Messages'

// Components
import NoResult from '@/components/common/NoResult'
import ButtonLoader from '@/components/common/ButtonLoader';

import { scroller } from 'vue-scrollto/src/scrollTo'

// Icon
import InformationOutline from 'mdi-vue/InformationOutline';

import {
    mapClientFields
} from '@/store/modules/client';

// const {
//     mapMutations: mapSupportMutations,
//     mapActions: mapSupportActions,
//     mapState: mapSupportState
// } = createNamespacedHelpers('support');

export default {
    components: {
        InformationOutline,
        NoResult,
        ButtonLoader
    },
    mixins: [
        filtersMixin,
        validationMixin
    ],
    validations: {
        message: { required }
        // message: {
        //     required,
        //     minLength: minLength(2),
        //     maxLength: maxLength(2048)
        // }
    },
    data: () => ({
        message: '',
        loadingBtn: false,
        currentUnreadMsg: null,
        messages: []
    }),
    computed: {
        ...mapClientFields([
            'client_data.firstname',
            'client_data.lastname',
            'client_data.email'
        ]),
        orderid() {
            return this.$route.params.id
        }
    },
    mounted() {
        this.getData()
    },
    methods: {
        fileExists(url) {
            if (url) {
                const req = new XMLHttpRequest();
                req.open('GET', url, false);
                req.send();
                return req.status === 200;
            }
            return false;
        },
        messageClass(owner) {
            if (owner === 'client') {
                return 'user-message'
            }

            return 'support-message writer-message'
        },
        getUnreadMessage() {
            if (this.unread.length > 0) {
                this.currentUnreadMsg = this.unread[0].id;

                const el = document.getElementById(`item${this.currentUnreadMsg}`);
                const header = document.getElementById('header').getBoundingClientRect().height;
                const firstScrollTo = scroller();
                firstScrollTo(el, {
                    duration: 600,
                    offset: -header - 10
                });

                this.markAsRead()
            }
        },
        markAsRead(val) {
            return new Promise((resolve, reject) => {
                const vm = this
                Api.post('/api/cp/order-message/mark-as-read', {
                    orderid: this.orderid
                })
                    .then(() => {
                        vm.decrementTicket(vm.tickets.data[val].unread_messages.length)
                        vm.tickets.data[val].unread_messages = []
                        vm.$root.$emit('updateCounter');
                        resolve(true)
                    })
                    .catch((error) => {
                        reject(error)
                    })
            })
        },
        keyListner(e) {
            if (e.which === 13 && this.message.length) {
                this.sendMessage()
            }
        },
        async sendMessage() {
            const vm = this;
            vm.loadingBtn = true;

            Api.post('/api/cp/order-message/create-message', {
                orderid: vm.orderid,
                message: vm.message
            })
                .then(() => {
                    eventBus.$emit('showSnackBar', 'Your message has been received', 'success');
                    const newMessage = {
                        created_at_local: {
                            date: new Date()
                        },
                        owner: 'client',
                        type: 'message',
                        details: vm.message
                    }
                    vm.messages.push(newMessage)
                    vm.message = '';
                })
                .catch((error) => {
                    eventBus.$emit('showSnackBar', error.response.data.message, 'error');
                })
                .finally(() => {
                // clear up validation object
                    vm.$v.$reset()
                    // clear up loader
                    vm.loadingBtn = false
                })
        },
        validationMessage: validationMessage(formMessages),
        async getData() {
            this.dataLoading = true;
            const vm = this
            await Api.post('/api/cp/order-message/get-messages', {
                orderid: vm.orderid
            })
                .then((response) => {
                    vm.messages = response.data
                    console.log(vm.tickets)
                })
                .then(() => {
                    vm.dataLoading = false;
                })
        }
    }
}
</script>

<style lang="scss">
    .message-details {
        .tickets.page-content {
            max-width: 100%;
            padding: 0;

            .tickets {
                padding: 0;

                &_card__info {
                    width: 100%;
                    padding-top: 20px;
                }
                &_card__messages {
                    margin-top: 0;
                    padding-top: 0;
                    border-top: none;

                    &-item__content-header {
                        align-items: baseline;
                        &__time{
                            display: flex;
                            flex-direction: column;

                            span {
                                margin-bottom: 10px;
                            }
                        }
                    }
                }
            }
        }
        .no-result__title_question:before {
            background: url("~@/assets/img/svg/order-massages.svg");
            width: 144px;
            height: 94px;
        }
    }
</style>
