<template lang="html">
    <main-layout
        :settings-breadcrumbs="settingsBreadcrumbs"
    >
        <template #content>
            <section class="wrapper wrapper--xs">
                <div class="my-orders-details">
                    <loading-overlay v-if="dataLoading && !noResults" />

                    <div v-if="!dataLoading && !noResults">
                        <orders-card
                            :order="order"
                            :index="1"
                            :offset="1"
                            :page="1"
                            mode="details"
                            @updateData="onActionCompleted"
                            @onDeleted="onDeleted()"
                            @eventChangeTab="onChangeTab"
                        />

                        <!-- Order information Tabs -->
                        <div class="orders-details-tabs">
                            <div>
                                <div class="orders-details-tabs__tab-links">
                                    <a
                                        :class="{'active-tab-link': tabs['#details']}"
                                        href="#"
                                        class="button transparent-btn"
                                        @click.prevent="show(`#details`)"
                                    >
                                        <img
                                            src="@/assets/img/svg/list.svg"
                                            alt="Deadline"
                                            width="20px"
                                            height="20px"
                                            class="mdi"
                                        >
                                        <span>Details</span>
                                    </a>
                                    <a
                                        :class="{'active-tab-link': tabs['#files']}"
                                        href="#"
                                        data-tab="add-files"
                                        class="button transparent-btn"
                                        @click.prevent="show(`#files`)"
                                    >
                                        <img
                                            src="@/assets/img/svg/paperclip.svg"
                                            alt="Deadline"
                                            width="20px"
                                            height="20px"
                                            class="mdi"
                                        >
                                        <span>Files</span>
                                    </a>

                                    <a
                                        :class="{'active-tab-link': tabs['#messages']}"
                                        href="#"
                                        class="button transparent-btn"
                                        @click.prevent="show(`#messages`)"
                                    >
                                        <img
                                            src="@/assets/img/svg/mail.svg"
                                            alt="Deadline"
                                            width="20px"
                                            height="20px"
                                            class="mdi"
                                        >
                                        <span>Messages</span>
                                        <v-chip v-if="order.unread_messages && order.unread_messages.length > 0">
                                            {{ order.unread_messages.length }}
                                        </v-chip>
                                    </a>

                                    <a
                                        :class="{'active-tab-link': tabs['#tickets']}"
                                        href="#"
                                        class="button transparent-btn"
                                        @click.prevent="show(`#tickets`)"
                                    >
                                        <img
                                            src="@/assets/img/svg/support.svg"
                                            alt="Deadline"
                                            width="20px"
                                            height="20px"
                                            class="mdi"
                                        >
                                        <span>Help Center</span>
                                        <span
                                            v-if="ticketsNotification > 0"
                                            class="file__new"
                                        >
                                            {{ ticketsNotification }}
                                        </span>
                                    </a>
                                </div>
                            </div>

                            <div>
                                <div
                                    v-if="tabs['#details']"
                                    class="tab-content"
                                >
                                    <order-details
                                        :order="order"
                                    />
                                </div>

                                <div
                                    v-if="tabs['#files']"
                                    id="files"
                                    class="tab-content"
                                >
                                    <attachments />
                                </div>

                                <div
                                    v-if="tabs['#messages']"
                                    id="messages"
                                    class="orders-details-tabs__messages tab-content"
                                >
                                    <messages />
                                </div>

                                <div
                                    v-if="tabs['#tickets']"
                                    class="orders-details-tabs__support-container help-center tab-content"
                                >
                                    <header class="orders-details-tabs__tab-header">
                                        <p class="orders-details-tabs__tab-description">
                                            <img
                                                src="@/assets/img/svg/information.svg"
                                                alt="Deadline"
                                                width="20px"
                                                height="20px"
                                                class="mdi"
                                            >
                                            Here is the list of all support tickets open with regards to this project.
                                            You can create a new support ticket directly from this page.
                                        </p>
                                    </header>

                                    <div class="orders-details-tabs__support-tickets">
                                        <button
                                            class="btn-base btn-base_colored btn-base_normal"
                                            @click="scrollToSupportForm()"
                                        >
                                            New Ticket
                                        </button>

                                        <Tickets :orderid="order.orderid" />
                                    </div>

                                    <header
                                        id="create-ticket"
                                        class="orders-details-tabs__tab-header orders-details-tabs__tab-header__new"
                                    >
                                        <h2 class="orders-details-tabs__tab-title">
                                            New Ticket
                                        </h2>
                                    </header>
                                    <support-form :prop-order-id="order.orderid" />
                                </div>
                            </div>
                        </div>
                    </div>

                    <no-results
                        v-if="!dataLoading && noResults"
                        class="pt-5"
                        type="order-details"
                    />
                </div>
            </section>
        </template>
    </main-layout>
</template>

<script>
import { eventBus } from '@/helpers/event-bus'
import Api from '@/helpers/api/index.js';
import { scroller } from 'vue-scrollto/src/scrollTo'

import filtersMixin from '@/mixins/filtersMixin.js'

import {
    mapClientFields
} from '@/store/modules/client';
import {
    forEach
} from 'lodash'

// Components
import MainLayout from '@/layouts/MainLayout';
import Messages from '@/components/account/Messages'
import Tickets from '@/components/account/SupportTicket'
import Attachments from '@/components/account/Attachments'
import OrderDetails from '@/components/account/OrderDetails'
import SupportForm from '@/components/support/SupportTicketForm'
import LoadingOverlay from '@/components/LoadingOverlay';
import OrdersCard from '@/components/account/MyOrdersCard'

export default {
    components: {
        MainLayout,
        LoadingOverlay,
        Messages,
        Tickets,
        OrderDetails,
        Attachments,
        SupportForm,
        OrdersCard
    },
    mixins: [
        filtersMixin
        // orderQuoteMixin
    ],
    data: () => ({
        actions: false,
        dataLoading: false,
        order: {},
        tabs: {
            '#details': true,
            '#files': false,
            '#messages': false,
            '#tickets': false
        },
        toggling: false,
        noResults: false,
        ticketsNotification: null
    }),
    computed: {
        ...mapClientFields([
            'client_data.id'
        ]),
        orderid() {
            return this.$route.params.id
        },
        settingsBreadcrumbs() {
            return {
                title: 'Order Details',
                steps: [
                    {
                        title: 'Home',
                        url: '/'
                    },
                    {
                        title: 'Account',
                        url: '/cp/account'
                    },
                    {
                        title: 'My Orders',
                        url: '/cp/orders'
                    },
                    {
                        title: this.orderid,
                        url: ''
                    }
                ],
                additionalClass: 'bc-horizontal'
            }
        }
    },
    watch: {
        $route(to, from) {
            if (to.hash !== from.hash) {
                this.show(to.hash)
            }
        }
    },
    async mounted() {
        this.$root.$on('updateCounter', () => {
            this.getCounterSupportTickets()
        })
        if (this.$route.hash === '#files' || this.$route.hash === '#messages') {
            this.show(this.$route.hash)
        }
        this.getOrderData()
    },
    deactivated() {
        this.$destroy()
    },
    methods: {
        show(selectedTab) {
            const vm = this
            forEach(vm.tabs, (value, key) => {
                vm.tabs[key] = false
            })
            this.tabs[selectedTab] = true
        },
        getOrderData() {
            const vm = this
            vm.dataLoading = true
            // setTimeout(function(){
            Api.post('/api/cp/order/fetch', {
                id: vm.$route.params.id
            })
                .then((response) => {
                    vm.order = response.data
                    vm.dataLoading = false
                    vm.order.showTools = true

                    // scroll up
                    // const firstScrollTo = scroller()
                    // firstScrollTo('#start')
                })
                .catch((error) => {
                    if (error.response.status === 404) {
                        this.noResults = true
                    }
                })
                .finally(() => {
                    vm.dataLoading = false
                })
            this.getCounterSupportTickets()
        },
        getCounterSupportTickets() {
            const vm = this
            Api.post('/api/cp/ticket-support/list-tickets', {
                orderid: vm.orderid,
                per_page: 999,
                clientid: vm.id
            })
                .then((response) => {
                    vm.updateCounterSupportTickets(response.data.data)
                })
        },
        updateCounterSupportTickets(arr) {
            let counter = 0
            arr.forEach((ticket) => {
                if (ticket.unread_messages.length > 0) {
                    counter += 1
                }
            })
            this.ticketsNotification = counter
        },
        updateOrderData() {
            const vm = this
            Api.post('/api/cp/order/fetch', {
                id: vm.$route.params.id
            })
                .then((response) => {
                    vm.order = response.data
                })
        },
        onActionCompleted(value) {
            eventBus.$emit('showSnackBar', value, 'success')
            this.updateOrderData()
        },
        onDeleted() {
            this.$router.push({ name: 'orders' })
        },
        onChangeTab(val) {
            this.show(val)
        },
        scrollToSupportForm() {
            const supportForm = document.querySelector('#create-ticket')
            if (supportForm) {
                const ScrollTo = scroller();
                ScrollTo(supportForm, {
                    duration: 600,
                    offset: -85
                });
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.order .tab-wrapper .tab {
    display: inline;
}

.help-center {
    .tickets {
        padding: 0;
        margin-top: 30px;
    }

    .orders-details-tabs__support-tickets {
        padding: 30px 25px 10px;
        padding-top: 0;
    }

    .support-form {
        margin: 0;
        box-shadow: none;
    }
}
</style>
