                        </div>
                        <div
                            v-if="detailsShortActive===true"
                            class="detail-value"
                        >
                            {{ detailsShort }} <a
                                href="#"
                                @click.prevent="detailsShortActive=!detailsShortActive"
                            >More</a>
                        </div>
                        <div
