                <span class="grand-total--price">{{ order.total | money }}</span>
                <span>{{ order.batchid }}</span>
            </div>
        </footer>
    </div>
</template>

<script>
import Api from '@/helpers/api/index.js';
import filtersMixin from '@/mixins/filtersMixin.js'
import {
    find, truncate
} from 'lodash'

// Icon
import InformationOutline from 'mdi-vue/InformationOutline';
import FileDocumentOutline from 'mdi-vue/FileDocumentOutline'

export default {
    components: {
        InformationOutline,
        FileDocumentOutline
    },
    mixins: [
        filtersMixin
    ],
    props: {
        order: {
            type: Object
        }
    },
    data() {
        return {
            projects: null,
            turnarounds: null,
            levels: null,
            subjects: null,
            styles: null,
            extrasRates: null,
            detailsShortActive: true
        }
    },
    computed: {
        detailsShort() {
            return truncate(this.order.details, { length: '50' })
        }
    },
    mounted() {
        this.getData()
    },
    methods: {
        getLabel(array, value) {
            const found = find(array, { value })
            return found ? found.text : ''
        },
        getData() {
            new Promise((resolve, reject) => {
                const vm = this
                Api.get('/api/data/order')
                    .then((response) => {
                        vm.projects = response.data.type
                        vm.turnarounds = response.data.turnaround
                        vm.levels = response.data.level
                        vm.subjects = response.data.subject
                        vm.styles = response.data.style
                        vm.extrasRates = response.data.extras

                        resolve('success')
                    })
                    .catch((err) => {
                        reject(err)
                    })
            })
        }
    }
}
</script>
